.header {
  &__inner {
    padding: 38px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  // &__right{
  //   display: flex;
  //   align-items: center;
  //   justify-content: space-between;
  // }
  &__right-cart{
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    margin-right: 24px;
  }
  .logo {
    margin-right: 40px;
    transition: all 0.3s;
    &:hover {
      transform: scale3d(0.96, 0.96, 1.01);
    }
  }
  .nav {
    &__list {
      display: flex;
    }
    &__item {
      &:not(:last-child) {
        margin-right: 22px;
      }
    }
    &__link {
      color: var(--dark-color);
      font-weight: 400;
      font-size: 18px;
      line-height: 30px;
      transition: all 0.3s ease;
      &:hover {
        color: var(--accent-color);
      }
    }
  }
}
