.btn {
  display: inline-block;
  border: none;
  background-color: var(--accent-color);
  border-radius: 14px;
  color: var(--light-color);
  padding: 18px 24px;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  transition: all 0.3s;
  &:hover {
    background-color: var(--hover-color);
    transform: translate3d(0px, -3px, 0.01px);
  }
}
